<template>
    <div class="shows" v-if="initialized">
        <p class="md-headline title">On Deck</p>
        <div class="show-grid">
            <div class="shows horizontal-scroll">
                <plex-card
                    v-for="show in deck" :key="show.title"
                    @click="download(show)"
                    class="wide-card plex-card"
                    :title="show.title"
                    :caption="caption(show.Media[0].Part[0].file)"
                    to="/"
                    :image="show.thumb"/>
            </div>
        </div>
        <p class="md-headline title">Shows</p>
        <div class="show-grid">
            <div class="shows">
                <plex-card
                    v-for="show in shows" :key="show.title"
                    class="plex-card"
                    :title="show.title"
                    :to="`/show/?id=${show.key}`"
                    :image="show.info.thumb"/>
            </div>
        </div>
    </div>
</template>

<script>
import ServerApi from "@/js/ServerApi";
import PlexCard from "../components/PlexCard";

console.log(ServerApi);

export default {
    props: {
        initialized: Boolean,
    },
    name: 'shows',
    components: {PlexCard},
    data() {
        return {
            shows: [],
            deck: [],
        }
    },
    async mounted() {
        if (this.initialized) {
            this.initialize();
        } else {
            ServerApi.addEventListener('authenticated', this.initialize);
        }
    },
    methods: {
        async download(show) {
            let file = show.Media[0].Part[0].file;
            console.log('Download', file);
            let {token} = await ServerApi.fileToken(file);
            console.log('Token', token);
            let a = document.createElement('a');
            // let win = window.open(ServerApi.fileUrl(token));
            a.href = ServerApi.fileUrl(token);
            a.setAttribute('download', file.split('/')[file.split('/').length - 1]);
            // a.setAttribute('target', '_blank');
            console.log(a);
            a.click();
        },
        caption(file) {
            let p = file.split('/');
            return p[p.length - 1];
        },
        async initialize() {
            if (localStorage.getItem('showsCache') !== null)
                this.shows = JSON.parse(localStorage.showsCache);
            if (localStorage.getItem('deckCache') !== null)
                this.deck = JSON.parse(localStorage.deckCache);

            let shows = await ServerApi.shows();
            this.shows = shows.filter(s => s.info.thumb);
            localStorage.showsCache = JSON.stringify(this.shows);
            console.log("shows", this.shows);

            this.deck = await ServerApi.onDeck();
            localStorage.deckCache = JSON.stringify(this.deck);
            console.log("deck", this.deck);
        }
    },
    beforeDestroy() {
        ServerApi.removeEventListener('authenticated', this.initialize)
    },
    watch: {
        $route() {
            this.initialize();
        }
    }
}
</script>

<style scoped>
.shows {
    /*overflow: hidden !important;*/
}

.horizontal-scroll {
    overflow-x: auto !important;
    white-space: nowrap;
}

.title {
    text-align: center;
}

.show-grid {
    position: relative;
    left: 5px;
    max-width: 1000px;
    display: block;
    margin: 0 auto;
}

.plex-card {
    display: inline-block;
    width: calc(100% / 3 - 10px);
    margin-right: 10px;
    margin-bottom: 20px;
}

.wide-card {
    width: calc(100% / 2 - 10px);
}

@media (min-width: 700px) {
    .plex-card {
        width: calc(100% / 4 - 10px);
    }
}

@media (min-width: 900px) {
    .plex-card {
        width: calc(100% / 5 - 10px);
    }
}


</style>